import getMuiTheme from 'material-ui/styles/getMuiTheme'

const muiTheme = getMuiTheme({
  palette: {
    primary1Color: '#3F51B5',
    primary2Color: '#303F9F',
    textColor: '#212121'
  },
  appBar: {
    height: 70
  }
})

export default muiTheme



